body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1,h2,h3,h4,h5,h6{margin-bottom:.5rem;font-weight:500;line-height:1.2}h1{font-size:2.5rem}h2{font-size:2rem}h3{font-size:1.75rem}h4{font-size:1.5rem}h5{font-size:1.25rem}h6{font-size:1rem}html{box-sizing:border-box}*,*::before,*::after{box-sizing:inherit}body{font:normal normal 400 16px "Lato",sans-serif;color:#484f54;background-color:#f6f7f8}.container{margin:1rem}

h1,h2,h3,h4,h5,h6{margin-bottom:.5rem;font-weight:500;line-height:1.2}h1{font-size:2.5rem}h2{font-size:2rem}h3{font-size:1.75rem}h4{font-size:1.5rem}h5{font-size:1.25rem}h6{font-size:1rem}.button{position:relative;display:flex;justify-content:center;align-items:center;padding:.375rem .75rem;font-family:"Lato",sans-serif;font-size:1rem;line-height:1.5;white-space:nowrap;font-weight:400;border:1px solid transparent;box-shadow:inset 0 1px 0 rgba(255,255,255,0.15),0 1px 1px rgba(0,0,0,0.075);-webkit-transition:color 0.15s ease-in-out,background-color 0.15s ease-in-out,border-color 0.15s ease-in-out,box-shadow 0.15s ease-in-out;transition:color 0.15s ease-in-out,background-color 0.15s ease-in-out,border-color 0.15s ease-in-out,box-shadow 0.15s ease-in-out}.button.primary{background-color:#1f8be2;border:1px solid #1f8be2;color:#fff}.button.primary:focus{outline:0;box-shadow:0 0 0 0.2rem rgba(31,139,226,0.25);background-color:#1870b6;border-color:#1870b6}.button.primary:hover{text-decoration:none;background-color:#1870b6;border-color:#1870b6}.button.primary:disabled{opacity:.65;box-shadow:none}.button.primary:not(:disabled):active{box-shadow:inset 0 3px 5px rgba(0,0,0,0.125);background-color:#125489}.button.primary:not(:disabled):active:focus{outline:0;box-shadow:0 0 0 0.2rem rgba(31,139,226,0.25),inset 0 3px 5px rgba(0,0,0,0.125)}.button.secondary{background-color:#9eacb7;border:1px solid #9eacb7;color:#fff}.button.secondary:focus{outline:0;box-shadow:0 0 0 0.2rem rgba(31,139,226,0.25);background-color:#8193a1;border-color:#8193a1}.button.secondary:hover{text-decoration:none;background-color:#8193a1;border-color:#8193a1}.button.secondary:disabled{opacity:.65;box-shadow:none}.button.secondary:not(:disabled):active{box-shadow:inset 0 3px 5px rgba(0,0,0,0.125);background-color:#667a89}.button.secondary:not(:disabled):active:focus{outline:0;box-shadow:0 0 0 0.2rem rgba(31,139,226,0.25),inset 0 3px 5px rgba(0,0,0,0.125)}

.card{position:relative;background-color:#fff;width:100%;border:none;margin-top:10px;box-shadow:0 0.5rem 1rem rgba(0,0,0,0.35)}

.card__container{padding:10px}

.card__footer{width:100%;height:55px;margin-top:10px;border-top:1px solid #dbe0e4}

.title{text-align:center}.title h4{font-size:2.18em}

.card-container__text{padding:10px;text-align:justify}.about__footer{display:flex;justify-content:center;align-items:center}

h1,h2,h3,h4,h5,h6{margin-bottom:.5rem;font-weight:500;line-height:1.2}h1{font-size:2.5rem}h2{font-size:2rem}h3{font-size:1.75rem}h4{font-size:1.5rem}h5{font-size:1.25rem}h6{font-size:1rem}.profile{position:relative;background-color:#fff;margin-top:-194px;height:400px;width:100%;border:none;box-shadow:0 0.5rem 1rem rgba(0,0,0,0.35)}.profile__image{position:absolute;left:0;right:0;bottom:80%;width:150px;margin-left:auto;margin-right:auto}.profile__image img{width:150px;height:150px;border-radius:50%;box-shadow:0 0.5rem 1rem rgba(0,0,0,0.35)}.profile__container{padding:10px}.container__infos{max-width:100%;margin-top:68px;text-align:center}.container__infos--name{position:relative;display:block;font-size:2.18rem;padding-top:10px}.container__infos--job{font-size:1.56em;text-transform:capitalize;margin-top:10px}.profile__contact{max-width:100%;font-size:1rem;margin-left:50px}.profile__contact .contact__mail,.profile__contact .contact__skype,.profile__contact .contact__phone,.profile__contact .contact__place{margin-top:10px}.contact__mail span,.contact__skype span,.contact__phone span,.contact__place span{margin-left:10px}.profile__social-media{margin-top:60px;text-align:center}.social-media__icon{display:inline;cursor:pointer}.social-media__icon:not(:first-of-type){margin-left:10px}@media (min-width: 789px){.profile{height:400px}}

.skill__container{display:flex;justify-content:space-around;align-items:center;flex-wrap:wrap}.container__technology{display:flex;flex-direction:column;justify-content:center;align-content:center;text-align:center;min-width:100px;height:100px}.technology__svg{width:100%;height:50px}.technology__label--angular{color:#dd0031}.technology__label--reactjs{color:#61dafb}.technology__label--redux{color:#764abc}.technology__label--nodejs{color:#689f63}.technology__label--html5{color:#e44d26}.technology__label--css3{color:#264de4}

.toolbar{position:relative;display:block;width:100%;height:370px;background:url(/my-pages/static/media/toolbar_background_small.fe327b8f.png) no-repeat;background-position:center;background-size:cover;opacity:0.8}

